.section-featured-blog {
	.featured-blog-list {
		position: relative;
	}
	.article-title {
		a {
			color: var(--text-color);
			font-size: 20px;
			line-height: 1.4em;
			font-weight: 500;
			display: block;
		}
	}
	.article-info {
		color: var(--subtext-color);
		font-size: 13px;
		line-height: 1.4em;
	}
	.tns-nav {
		margin-top: 30px;
	}
}